I;EAdJ;IAeQ,YAAA;IACA,gBAAA;EAuBN;AACF;;AArBA;EACI,WAAA;EACA,iBAAA;EACA,cAAA;EACA,kBAAA;EACA,QAAA;EACA,kBAAA;EACA,MAAA;EACA,WAAA;EACA,qBAAA;AAwBJ;AAvBI;EACI,YAAA;AAyBR;;AArBA;EACI,YAAA;EACA,iBAAA;AAwBJ;AAvBI;EACI,qBAAA;EACA,sBAAA;AAyBR;AAvBI;EACI,qBAAA;EACA,sBAAA;EACA,iBAAA;AAyBR;;AArBI;EACI,YAAA;EACA,aAAA;AAwBR;AAtBI;EACI,WAAA;EACA,YAAA;AAwBR;AAtBI;EACI,WAAA;EACA,YAAA;AAwBR","sourcesContent":["$blue: #3399ff;\r\n$lightblue: #66b3ff;\r\n$darkblue: #336699;\r\n$red: #dd3333;\r\n$green: #00a264;\r\n$purple: #6b3ea8;\r\n$color-body: rgb(24, 25, 26);\r\n$color-surface: rgb(36, 37, 38);\r\n$color-comment: rgb(58, 59, 60);\r\n\r\n$img-dir: \"../images\";\r\n\r\n// Breakpoint settings and methods\r\n\r\n$breakpoint-mobile: 640px;\r\n\r\n@mixin breakpoint($view) {\r\n    @if $view == mobile {\r\n        @media (max-width: $breakpoint-mobile) { @content; }\r\n    }\r\n    @else {\r\n        @media (min-width: $breakpoint-mobile + 1px) { @content; }\r\n    }\r\n}\r\n\r\n// Spacing settings and methods\r\n\r\n$spacing-mobile: 1rem;\r\n$spacing-monitor: 3.5rem;\r\n\r\n@mixin spacing($prop) {\r\n    #{$prop}: $spacing-mobile;\r\n    @include breakpoint(screen) {\r\n        #{$prop}: $spacing-monitor;\r\n    }\r\n}\r\n\r\n// Top Level (Global)\r\n\r\n:root {\r\n    font: normal 100% sans-serif;\r\n    font-size: calc(100vw / 25);\r\n    color: white;\r\n    @include breakpoint(screen) {\r\n        font-size: calc(100vw / 70);\r\n    }\r\n}\r\n\r\na,\r\n.a {\r\n    color: $blue;\r\n    text-decoration: underline;\r\n    cursor: pointer;\r\n    &:active {\r\n        color: $purple;\r\n    }\r\n    &:hover {\r\n        color: $lightblue;\r\n        border-color: $lightblue;\r\n    }\r\n}\r\n\r\nfieldset {\r\n    margin-left: 0;\r\n    margin-right: 0;\r\n    padding: 5px 10px 10px 10px;\r\n    border: 1px solid #ccc;\r\n}\r\nlegend {\r\n    color: #666;\r\n}\r\ninput[type=\"text\"],\r\ninput[type=\"password\"],\r\ntextarea,\r\nselect,\r\n.inputfield {\r\n    padding: 3px 1px 3px 2px;\r\n    margin-bottom: 1px;\r\n    border-width: 1px;\r\n    border-style: solid;\r\n    border-color: #666 #bbb #bbb #666;\r\n    background: white;\r\n    background: rgba(255, 255, 255, 0.7);\r\n    border-radius: 2px;\r\n    outline: none;\r\n}\r\ntextarea {\r\n    font-family: monospace;\r\n}\r\nselect {\r\n    padding: 2px;\r\n}\r\noptgroup {\r\n    padding-top: 2px;\r\n    font-weight: normal;\r\n    font-style: italic;\r\n    color: #777;\r\n    background-color: #eee;\r\n}\r\noptgroup > option {\r\n    padding-left: 20px;\r\n    background-color: #fff;\r\n    color: black;\r\n}\r\noptgroup > option:first-child {\r\n    margin-top: 2px;\r\n}\r\n\r\n// .button-blue {\r\n// \t@link-color: darken(@lightblue, 16%);\r\n// \tcolor:rgba(255,255,255,.93); text-shadow:0 -1px @link-color, 0 1px @lightblue;\r\n// \tbackground:@link-color; background:-moz-linear-gradient(top, @lightblue 50%, @link-color 50%); background:-webkit-gradient(linear, left top, left bottom, color-stop(50%,@lightblue), color-stop(50%,@link-color));\r\n// \tborder-color:darken(@lightblue, 10%) darken(@link-color, 5%) darken(@link-color, 5%) darken(@lightblue, 5%);\r\n// \tbox-shadow:0 1px 1px rgba(0,0,0,.2);\r\n// \t&:hover { border-color:darken(@link-color, 25%); }\r\n// \t&:active { box-shadow:none; background:darken(@lightblue, 5%); border-color:darken(@link-color, 12%) darken(@lightblue, 12%) darken(@lightblue, 12%) darken(@link-color, 12%); }\r\n// }\r\nbutton:not(.plain),\r\ninput[type=\"button\"],\r\ninput[type=\"submit\"],\r\ninput[type=\"reset\"],\r\n.faux-button {\r\n    padding: 3px 10px;\r\n    color: #444;\r\n    text-shadow: 0 -1px #dadada, 0 1px #eee;\r\n    background: #ddd;\r\n    background: -moz-linear-gradient(top, #eee 50%, #ddd 50%);\r\n    background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, #eee), color-stop(50%, #ddd));\r\n    border-width: 1px;\r\n    border-style: solid;\r\n    border-color: #ddd #aaa #aaa #ddd;\r\n    border-radius: 2px;\r\n    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);\r\n    cursor: pointer;\r\n    &:hover {\r\n        border-color: #777;\r\n    }\r\n    &:active,\r\n    &.active {\r\n        box-shadow: none;\r\n        background: #ddd;\r\n        border-color: #aaa #ccc #ccc #aaa;\r\n    }\r\n    &[disabled=\"disabled\"] {\r\n        color: #bbb;\r\n        cursor: not-allowed;\r\n    }\r\n    &.submit:hover {\r\n        background: $green;\r\n        border-color: #016c43;\r\n        color: white;\r\n        text-shadow: none;\r\n    }\r\n    &.cancel:hover {\r\n        background: $red;\r\n        border-color: darken($red, 15%);\r\n        color: white;\r\n        text-shadow: none;\r\n    }\r\n}\r\n\r\nimg {\r\n    vertical-align: middle;\r\n}\r\n\r\nh1 {\r\n    font-weight: 300;\r\n    line-height: 1.1;\r\n    margin-top: 0;\r\n    margin-bottom: 0.75em;\r\n    color: whitesmoke;\r\n    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.2);\r\n}\r\nh2 {\r\n    margin-bottom: 0.5em;\r\n    font-weight: normal;\r\n    color: whitesmoke;\r\n}\r\nh3 {\r\n    margin: 1em 0 0.5em;\r\n}\r\nh4 {\r\n    margin: 1em 0 0.5em;\r\n}\r\nh5 {\r\n    margin: 1em 0 0.5em;\r\n    font-size: 1.25rem;\r\n}\r\nh6 {\r\n    margin: 1em 0 0.5em;\r\n}\r\n\r\n// Page Layouts\r\n\r\n// A template for fullwidth elements;\r\n// Top-level <body> and other full-width elements.\r\n.grid-page {\r\n    display: grid;\r\n    grid-template-columns: [margin-start] $spacing-mobile [narrow-gutter-start] $spacing-monitor - $spacing-mobile [wide-gutter-start] auto [wide-gutter-end] $spacing-monitor - $spacing-mobile [narrow-gutter-end] $spacing-mobile [margin-end];\r\n    & > * {\r\n        grid-column: narrow-gutter-start / narrow-gutter-end;\r\n        @include breakpoint(screen) {\r\n            grid-column: wide-gutter-start / wide-gutter-end;\r\n        }\r\n    }\r\n}\r\n.fullwidth {\r\n    grid-column: margin-start / margin-end;\r\n}\r\n\r\nbody {\r\n    min-height: 100vh;\r\n    box-sizing: border-box;\r\n    padding: 0;\r\n    background: {\r\n        color: $color-body;\r\n    }\r\n    text-align: left;\r\n    @extend .grid-page;\r\n    grid-template-rows: min-content auto max-content;\r\n    > header, > main, > footer {\r\n        @include spacing(padding-top);\r\n        @include spacing(padding-bottom);\r\n    }\r\n}\r\n\r\nbody > header {\r\n    @extend .fullwidth;\r\n    background-color: $color-body;\r\n    border-bottom: 1px solid $color-surface;\r\n}\r\n#navmenu {\r\n    height: 30px;\r\n    button {\r\n        position: fixed;\r\n        z-index: 9; // Behind modal overlay\r\n        @include spacing(\"top\");\r\n        @include spacing(\"left\");\r\n        margin: 0;\r\n        padding: 0;\r\n        border: none;\r\n        text-align: left;\r\n        cursor: pointer;\r\n        background-color: transparent;\r\n        display: flex;\r\n        align-items: center;\r\n        &:after {\r\n            content: \"\";\r\n            width: 163px;\r\n            height: 18px;\r\n            margin: 2px 0 0 8px;\r\n            background: transparent url(\"#{$img-dir}/h1_condensed.png\") no-repeat scroll 0 0;\r\n        }\r\n        &:hover::after {\r\n            background-position: 0 -19px;\r\n        }\r\n        &.active svg {\r\n            color: black !important;\r\n        }\r\n        @include breakpoint(screen) {\r\n            margin-left: -38px; // hamburger:30px; bgimg padding:8px;\r\n        }\r\n    }\r\n    .modal,\r\n    .modal-overlay {\r\n        z-index: 7;\r\n    }\r\n    .modal-content {\r\n        z-index: 8;\r\n    }\r\n}\r\n#login {\r\n    .modal-content {\r\n        width: 225px;\r\n        margin-right: auto;\r\n        margin-left: auto;\r\n        @extend .dark;\r\n        background-color: transparent !important;\r\n    }\r\n    form {\r\n        margin-top: -1em;\r\n        display: flex;\r\n        flex-direction: column;\r\n        align-items: flex-start;\r\n        & > * {\r\n            margin-top: 1em;\r\n        }\r\n        input {\r\n            width: 100%;\r\n            padding: 6px 0 6px 8px;\r\n            font-size: 14px;\r\n            color: #666;\r\n            background: white;\r\n            background: -moz-linear-gradient(top, #e0e0e0, white 7px);\r\n            background: -webkit-gradient(\r\n                linear,\r\n                left top,\r\n                left bottom,\r\n                color-stop(0%, #e0e0e0),\r\n                color-stop(100%, white)\r\n            );\r\n            border-width: 0;\r\n            border-radius: 3px;\r\n        }\r\n        button {\r\n            padding: 5px 15px;\r\n            font-size: 15px;\r\n            font-weight: bold;\r\n            border-width: 0;\r\n            box-shadow: 1px 1px 3px 1px black;\r\n            -moz-box-shadow: 1px 1px 3px 1px black;\r\n            -webkit-box-shadow: 1px 1px 3px 1px black;\r\n            &:active {\r\n                margin: 1px 0 0 1px;\r\n                box-shadow: 0 0 3px 1px black;\r\n                -moz-box-shadow: 0 0 3px 1px black;\r\n                -webkit-box-shadow: 0 0 3px 1px black;\r\n            }\r\n            & + button {\r\n                margin-left: 1em;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\nbody > #content {\r\n    @include spacing(padding-top);\r\n    @include spacing(padding-bottom);\r\n}\r\n\r\nbody > footer {\r\n    @extend .fullwidth;\r\n    @extend .grid-page;\r\n    position: relative;\r\n    z-index: 1;\r\n    @include spacing(padding-top);\r\n    @include spacing(padding-bottom);\r\n    border-top: 1px solid $color-surface;\r\n    color: #999;\r\n    a {\r\n        color: #aaa;\r\n    }\r\n    a:hover {\r\n        color: #ccc;\r\n    }\r\n    h5 {\r\n        margin: 0 0 5px;\r\n        font-size: 15px;\r\n    }\r\n    ul {\r\n        margin: 0;\r\n        padding: 0;\r\n        list-style: none;\r\n        li {\r\n            margin: 0;\r\n            padding: 0;\r\n        }\r\n    }\r\n    .about,\r\n    .featured {\r\n        display: none;\r\n        li {\r\n            a {\r\n                display: block;\r\n                margin: 0 14px 7px 0;\r\n                font-size: 14px;\r\n                background-position: left center;\r\n                background-repeat: no-repeat;\r\n                .link-twitter {\r\n                    padding-left: 20px;\r\n                    background: url(\"#{$img-dir}/twitter_sm.png\") no-repeat left center;\r\n                }\r\n            }\r\n        }\r\n    }\r\n    .featured ul li {\r\n    }\r\n    #diorama {\r\n        width: 100%;\r\n        height: 20px; // Functions as padding\r\n        div {\r\n            position: absolute;\r\n            background-image: url(\"#{$img-dir}/footer_diorama.png\");\r\n        }\r\n    }\r\n}\r\n\r\n// Custom Classes\r\n\r\n.dark {\r\n    background-color: black;\r\n    color: white;\r\n    a {\r\n        color: white;\r\n        &:hover {\r\n            color: lightgray;\r\n        }\r\n    }\r\n}\r\n.light {\r\n    background-color: white;\r\n    color: black;\r\n    a {\r\n        color: black;\r\n        &:hover {\r\n            color: rgb(46, 46, 46);\r\n        }\r\n    }\r\n}\r\n.red {\r\n    color: $red;\r\n    &:hover {\r\n        color: #f17878;\r\n    }\r\n}\r\n\r\n.modal {\r\n    position: fixed;\r\n    z-index: 10;\r\n    top: 0;\r\n    right: 0;\r\n    bottom: 0;\r\n    left: 0;\r\n    display: grid;\r\n}\r\n.modal-container {\r\n    &.modal-enter {\r\n        opacity: 0;\r\n    }\r\n    &.modal-enter-active {\r\n        opacity: 1;\r\n        transition: all 500ms;\r\n    }\r\n    &.modal-exit {\r\n        opacity: 1;\r\n    }\r\n    &.modal-exit-active {\r\n        opacity: 0;\r\n        transition: opacity 500ms;\r\n    }\r\n}\r\n.modal-overlay {\r\n    width: 100%;\r\n    height: 100%;\r\n    z-index: 10; /* places the modal overlay between the main page and the modal content*/\r\n    background-color: rgba(0, 0, 0, 0.95);\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    margin: 0;\r\n    padding: 0;\r\n    transition: all 0.3s;\r\n}\r\n.modal-content {\r\n    width: auto;\r\n    margin: 0.5rem;\r\n    padding: 2rem;\r\n    position: relative;\r\n    z-index: 11; /* places the modal dialog on top of overlay */\r\n    top: 0;\r\n    left: 0;\r\n    display: flex;\r\n    flex-direction: column;\r\n    background-color: white;\r\n    h5 {\r\n        margin-top: 0;\r\n    }\r\n    @media (min-width: $breakpoint-mobile) {\r\n        margin: auto;\r\n        max-width: 500px;\r\n    }\r\n}\r\n.modal-close {\r\n    color: #aaa;\r\n    line-height: 50px;\r\n    font-size: 80%;\r\n    position: absolute;\r\n    right: 0;\r\n    text-align: center;\r\n    top: 0;\r\n    width: 70px;\r\n    text-decoration: none;\r\n    &:hover {\r\n        color: black;\r\n    }\r\n}\r\n\r\n.user {\r\n    height: 20px;\r\n    line-height: 20px;\r\n    .user-username {\r\n        display: inline-block;\r\n        vertical-align: middle;\r\n    }\r\n    .user-avatar.thumbnail {\r\n        display: inline-block;\r\n        vertical-align: middle;\r\n        margin-right: 5px;\r\n    }\r\n}\r\n.user-avatar {\r\n    &.big img {\r\n        width: 144px;\r\n        height: 144px;\r\n    }\r\n    &.icon img {\r\n        width: 48px;\r\n        height: 48px;\r\n    }\r\n    &.thumbnail img {\r\n        width: 20px;\r\n        height: 20px;\r\n    }\r\n}\r\n"],"sourceRoot":""}]);
